<template>
	<view class="container achievement">
		<watermark></watermark>
		<view class="fixedTop">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="_getChange" height="110" bg-color="#03958C" inactive-color="#fff" active-color="#fff" :active-item-style="activeStyle"></u-tabs>
		</view>
		<view class="tabs_item" v-if="current==0">
			<barDropDown :key='0' :chartData="chartData" :unitText="'套'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<template>
				<achiev :rank="rank" :signList="signList" :tabIndex="0"></achiev>
			</template>
		</view>
		<view class="tabs_item" v-else-if="current==1">
			<barDropDown :key='1' :chartData="chartData" :unitText="'万元'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<template>
				<achiev :rank="rank" :signList="signList" :tabIndex="1"></achiev>
			</template>
		</view>
		<view class="tabs_item" v-else-if="current==2">
			<barDropDown :key='2' :chartData="chartData" :unitText="'万元'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
			<template>
				<achiev :rank="rank" :signList="signList" :tabIndex="2"></achiev>
			</template>
		</view>
	</view>
	</view>
</template>

<script>
	import navTabs from '@/components/nav-tabs.vue';
	import barDropDown from '@/components/barDropDown.vue';
	import watermark from '@/components/watermark.vue';
	import achiev from './components/achiev.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				activeStyle: {
					fontSize:'36rpx'
				},
				current: 0,
				isshow: false,
				list: [{
					name: '认购'
				}, {
					name: '签约'
				}, {
					name: '回款'
				}],
				form: {
					tabCode: '', // tabs 0认购 1签约 2回款
					timeUnit: '2', // 时间统计 0年 1季 2月 3周
					time: '', // 当前统计格式 prev/next值
				},
				rank: {}, // 排名
				timeSelect: { // 柱状图选择
					prev: '',
					next: '',
				},
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				signList: [], // 签约列表
				// 柱状图
				chartData: {
					categories: [],
					series: [{
						data: []
					}],
				},
			}
		},
		onLoad() {
			this.getList()
		},
		methods: {
			// 获取列表
			getList() {
				let that = this
				that.form.tabCode = that.current
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2033', Object.assign({}, that.form, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.timeSelect.prev = res.data.data.histogram.pre
						that.timeSelect.next = res.data.data.histogram.next
						that.rank = res.data.data.rank
						that.chartData.series[0].data = res.data.data.histogram.yData
						that.$set(that.chartData, 'series', that.chartData.series)
						that.$set(that.chartData, 'categories', res.data.data.histogram.xData)
						// that.chartData.categories = res.data.data.histogram.xData
						// that.chartData.series[0].data = res.data.data.histogram.yData
						that.signList = that.etype == 3 ? that.signList.concat(res.data.data.listData.data) : res.data.data.listData.data
						that.totalPages = res.data.data.listData.totalPages
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
						bus.$emit('changeEcharts',that.chartData)
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				this.page.pageNum = 1
				this.etype = 2
				if (!this.loading) {
					this.loading = true
					this.getList()
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.totalPages > this.page.pageNum) {
					this.etype = 3
					this.page.pageNum++
					this.getList()
				}
			},
			_getChange(index) {
				this.current = index
				this.page.pageNum = 1
				this.form.time = ''
				this.signList = []
				this.getList()
			},
			// 柱状图左翻
			_prevTap() {
				this.form.time = this.timeSelect.prev
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图右翻
			_nextTap() {
				this.form.time = this.timeSelect.next
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.form.timeUnit = index
				this.form.time = ''
				this.etype = 0
				this.page.pageNum = 1
				this.getList()
			}
		},
		components: {
			barDropDown,
			watermark,
			achiev
		}
	}
</script>

<style lang="scss">
	page {
		background: #f6f6f6;
	}

	.achievement {
		.tabs_item {
			margin-top: 90rpx;
			padding-bottom: 30rpx;
		}
	}
</style>
